import { AppstoreOutlined, MailOutlined, SettingOutlined } from '@ant-design/icons';
import { Menu } from 'antd';
import { useState } from 'react';
import './Teoria.css';

const Teoria = () => {
  const [current, setCurrent] = useState('mail');

  return (

    <>

      <div >
        
        
        <h1 className="Titulo">
        
        Explicacin del mtodo grafico
      
        
        </h1>
        <p className="parrafo">
        <p className="parrafoList">
          <br/>
          &nbsp;El mtodo grafico sirve para resolver, con una precisin aceptable, una gran parte de los problemas de optimizacin lineal de dos variables. 
          Tiene dos etapas importantes, la primera es la determinacin de la regin admisible o realizable o factible (el conjunto de puntos que cumplen todas las restricciones) y la segunda es la bsqueda 
          del punto ptimo (o de los puntos ptimos) en la regin admisible. 
           <br/>
           La determinacin de la regin admisible es muy sencilla, pues se trata de obtener la interseccin de semiplanos (desigualdades) y de
           rectas (igualdades). Como generalmente las variables son no negativas, el estudio se hace nicamente en el primer cuadrante. El conjunto admisible estar a entonces limitado por semirrectas 
           (en este caso el conjunto admisible no es acotado) o por segmentos de recta. Los valores de las coordenadas de los vrtices se pueden determinar grficamente o de manera ms precisa, analticamente, 
           calculando la solucin de las dos ecuaciones (rectas) que determinan el vrtice (un vrtice corresponde a la nocin, que se ver posteriormente, de punto extremo de un convexo). 
           Una vez hallada la regin admisible se procede a buscar el ptimo. Se necesita entonces saber cmo vara la funcin objetivo y, sobre todo, en qu direccin mejora. Una manera sencilla consiste en dar dos valores 
           arbitrarios diferentes a z y dibujar las rectas (paralelas) correspondientes. Esto permite saber en qu sentido mejora el valor de z. Para cualquier otro valor de z, la recta correspondiente ser paralela. 
           nicamente queda por encontrar una de estas rectas paralelas, con el mejor valor posible de z y que pase al menos por un punto de la regin admisible.
           </p>
        </p>
      </div>
     



    </>


  );
};
export { Teoria };
